import React from 'react';
import { Image } from 'react-bootstrap';
import './Profile.css';

function Profile(props) {
    const item = props.dataParentToChild;
    return (
        <div className="profile-box">
            <Image roundedCircle fluid src={item.avatar_url} />
            <br />
            <div className="contact-details">
                <h2>{item.name}</h2>
                <p>{item.bio}</p>
                <p>Email: <a href={"mailto:" + process.env.REACT_APP_EMAIL}>{process.env.REACT_APP_EMAIL}</a></p>
                {/*<p>Phone Number: {process.env.REACT_APP_PHONE}</p>*/}
                <p>Github: <a href={process.env.REACT_APP_GITHUB} target="_blank" rel="noreferrer">{process.env.REACT_APP_USERNAME}</a></p>
                <p>Linkedin: <a href={process.env.REACT_APP_LINKEDIN} target="_blank" rel="noreferrer">{process.env.REACT_APP_USERNAME}</a></p>
                <p>Twitter: <a href={process.env.REACT_APP_TWITTER} target="_blank" rel="noreferrer">{process.env.REACT_APP_USERNAME}</a></p>
                <p>Facebook: {process.env.REACT_APP_FACEBOOK}</p>
            </div>
        </div>
    );
}

export default Profile;